<template>
    <section class="container">
        <!-- <loading :active.sync="isLoading" ></loading> -->
        <nav aria-label="breadcrumb">
        <ol class="breadcrumb bg-dark" >
            <li class="breadcrumb-item"><router-link to="/products" class="text-white"></router-link></li>
            <li class="breadcrumb-item">
                <!-- <a href="#" class="text-white">Library</a> -->
            <router-link class="text-white" :to="{ path: '/products', query: { category: product.category } }">{{product.category}}</router-link>                
            </li>
            <li class="breadcrumb-item active" aria-current="page">{{product.title}}</li>
        </ol>
        </nav>      
        <div class="row my-4">
            <div class="col-md-6">
                <div class="bg-contain" :style="{backgroundImage:`url('${product.imageUrl}')`}">

                </div>
            </div>
            <div class="col-md-6 pl-md-5 mt-md-0 mt-4 text-md-left text-center text-white" >
                <h3 class="text-warning text-shadow-red" >{{product.title}}</h3>
                <del class="text-white" v-if='product.origin_price > 0'>{{product.origin_price | currency}}</del>
                <h4 class='text-danger font-weight-bold ' v-if='product.origin_price > 0'>{{product.price | currency}}</h4>
                <h4 class='text-danger font-weight-bold ' v-if='product.origin_price == 0'></h4>                
                <h3 class="text-right" v-if="product.is_enable"></h3>
                <h4 class="text-right text-danger font-weight-bold" v-if="product.is_enable">{{product.price*product.num | currency}}</h4>
                <div v-if="product.is_enable">
                    <label  for="num"></label>
                    <select class="form-control " v-model="product.num" name="num">
                    <option :value='num' v-for='num in 10' :key='num' > {{num}} {{product.unit}} </option>
                    </select>  
                    <button
                    type="button"
                    class="btn btn-primary w-100 mt-3"
                    @click='addtoCart(product.id,product.num)'
                    :disabled="isdisabled"
                    ></button>                                      
                </div>

                <div class="mt-2">
                    <h4 class="text-warning"></h4>
                    <p>{{product.description}}</p>
                    <h4 class="text-warning"></h4>
                    <p>{{product.content}}</p>
                </div>
                <div v-if="!product.is_enable">
                    <h3 class="text-danger" ></h3>
                </div>                
                
            </div>
        </div>  
    </section >
</template>

<script>
import {mapGetters} from 'vuex';

export default {

    data(){
        return{
            product:{},  // id
            productId:'', // id
        }
    },
    computed:{
        ...mapGetters('cartsModules',['isdisabled','loadingItem'])
    },
    methods:{
        getProduct(){ //  id
            const apiPath = process.env.VUE_APP_APIPATH ; // config/dev.env.jsAPIPATH
            const customPath = process.env.VUE_APP_CUSTOMPATH; // config/dev.env.jsCUSTOMPATH
            const vm =this ;
            vm.$store.dispatch('updateLoading',true);
            const api = `${apiPath}/api/${customPath}/product/${vm.productId}`;  // idApi
            this.$http.get(api).then((response)=>{
                if(response.data.success){
                    vm.product = response.data.product;
                    vm.defaultNum();  //  product.num  1 
                    vm.$store.dispatch('updateLoading',false);
                }
            }) 
        },
        defaultNum(){ //  product.num  1 
            const vm =this;
            vm.product.num = 1;
        },
        addtoCart(id,qty=1){
            this.$store.dispatch('cartsModules/addtoCart',{id,qty});
        },                
    },
    created(){
        const vm =this;
        vm.productId = vm.$route.params.productId   // ProductDetail id
        vm.getProduct() ;  // id
    }    
}
</script>


