<template>
    <div>
        <!-- <loading :active.sync="isLoading" ></loading>         -->
        <div class="text-right">
            <button class="btn btn-primary mt-4" @click="openModal(true)" ></button>
        </div>
        <table class="table mt-4">
            <thead>
                <th width='120'></th>
                <th></th>
                <th width='120'></th>
                <th width='120'></th>
                <th width='100'></th>
                <th width='80'></th>
                <th width='80'></th>
            </thead>
            <tbody>
                <tr v-for="item in products" :key="item.id">
                    <td>{{ item.category}}</td>
                    <td>{{ item.title  }}</td>
                    <td class="text-right">{{ item.origin_price | currency}}</td>
                    <td class="text-right">{{ item.price | currency}}</td>
                    <td>
                        <span v-if="item.is_enable" class="text-success"></span>
                        <span v-else></span>
                        </td>
                    <td>
                        <button class="btn btn-outline-primary btn-sm" @click="openModal(false,item)"></button>
                    </td>
                    <td>
                        <button class="btn btn-danger btn-sm" @click="delModal(item)"></button>
                    </td>
                </tr>
            </tbody>
        </table>
        <Pagination  :paginationProp='pagination' @emit-update-page='getProduct'></Pagination>
        <!--  
        <nav aria-label="Page navigation example">
            <ul class="pagination">
                <li class="page-item" :class="{'disabled': !pagination.has_pre}"> 1
                    <a class="page-link" href="#" aria-label="Previous" @click.prevent='getProduct(pagination.current_page -1)'> 1 +1
                        <span aria-hidden="true">&laquo;</span>
                    </a>
                </li>
                <li class="page-item"  v-for="page in pagination.total_pages" :key="page" :class="{'active':pagination.current_page === page}"> .active
                    <a class="page-link" href="#" @click.prevent='getProduct(page)'>{{page}}</a></li>   
                <li class="page-item" :class="{'disabled': !pagination.has_next}"> 1
                    <a class="page-link" href="#" aria-label="Next" @click.prevent='getProduct(pagination.current_page +1)'>  1 +1
                        <span aria-hidden="true">&raquo;</span>
                    </a>
                </li>
            </ul>
        </nav>         -->
        <!-- Modal -->
        <div class="modal fade" id="productModal" tabindex="-1" role="dialog"
        aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog modal-lg" role="document">
                <div class="modal-content border-0">
                <div class="modal-header bg-dark text-white">
                    <h5 class="modal-title" id="exampleModalLabel">
                    <span></span>
                    </h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    <div class="row">
                    <div class="col-sm-4">
                        <div class="form-group">
                        <label for="image"></label>
                        <input type="text" class="form-control" id="image" 
                            placeholder="" v-model="tempProduct.imageUrl">
                        </div>
                        <div class="form-group">
                        <label for="customFile"> 
                            <i class="fas fa-sync fa-spin"  v-if="status.fileUploading"></i>
                        </label>
                        <input type="file" id="customFile" class="form-control"
                            ref="files" accept=".jpg, .jpeg, .png"
                            @change="uploadFile"
                            >
                        </div>
                        <img img="https://images.unsplash.com/photo-1483985988355-763728e1935b?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=828346ed697837ce808cae68d3ddc3cf&auto=format&fit=crop&w=1350&q=80"
                        class="img-fluid" :src="tempProduct.imageUrl" alt="">
                    </div>
                    <div class="col-sm-8">
                        <div class="form-group">
                        <label for="title"></label>
                        <input type="text" class="form-control" id="title"
                            placeholder="" v-model="tempProduct.title">
                        </div>

                        <div class="form-row">
                        <div class="form-group col-md-6">
                            <label for="category"></label>
                            <input type="text" class="form-control" id="category"
                            placeholder="" v-model="tempProduct.category">
                        </div>
                        <div class="form-group col-md-6">
                            <label for="price"></label>
                            <input type="unit" class="form-control" id="unit"
                            placeholder="" v-model="tempProduct.unit">
                        </div>
                        </div>

                        <div class="form-row">
                        <div class="form-group col-md-6">
                        <label for="origin_price"></label>
                            <input type="number" class="form-control" id="origin_price"
                            placeholder="" v-model="tempProduct.origin_price">
                        </div>
                        <div class="form-group col-md-6">
                            <label for="price"></label>
                            <input type="number" class="form-control" id="price"
                            placeholder="" v-model="tempProduct.price">
                        </div>
                        </div>
                        <hr>

                        <div class="form-group">
                        <label for="description"></label>
                        <textarea type="text" class="form-control" id="description"
                            placeholder="" v-model="tempProduct.description"></textarea>
                        </div>
                        <div class="form-group">
                        <label for="content"></label>
                        <textarea type="text" class="form-control" id="content"
                            placeholder="" v-model="tempProduct.content"></textarea>
                        </div>
                        <div class="form-group">
                        <div class="form-check">
                            <!-- is_enable = 1  0   :true-value="1"  checkbox  valuetrue is_enable 1 :false-value="0"  checkbox  valuefalse is_enable 0-->
                            <input class="form-check-input" type="checkbox"
                            id="is_enabled" 
                            v-model="tempProduct.is_enable"
                            :true-value="1"
                            :false-value="0">
                            <label class="form-check-label" for="is_enabled">
                            
                            </label>
                        </div>
                        </div>
                    </div>
                    </div>
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
                    <button type="button" class="btn btn-primary" @click="updateProduct"></button>
                </div>
                </div>
            </div>
        </div>
        <!-- Modal -->
        <div class="modal fade" id="delProductModal" tabindex="-1" role="dialog"
            aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog" role="document">
                <div class="modal-content border-0">
                <div class="modal-header bg-danger text-white">
                    <h5 class="modal-title" id="exampleModalLabel">
                    <span></span>
                    </h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                     <strong class="text-danger">{{ tempProduct.title }}</strong> ()
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
                    <button type="button" class="btn btn-danger"
                    @click="delProduct()"
                    ></button>
                </div>
                </div>
            </div>
        </div>
    </div>
</template>


<script>
import $ from 'jquery'; // jquery  ESLint $

import Pagination from '../../../components/Pagination' //Pagination 

export default {  // created()  
    data(){
        return{
            products:[],   // 
            pagination:{}, // 
            tempProduct:{}, //   modalinput  1  tempProduct
            isNew:false, //   isNewtrue    isNew  false
            isLoading:false, // <loading :active.sync="isLoading" ></loading> false
            status:{
                fileUploading:false,
            }
        }
    },
    components:{
        Pagination,
    },
    methods:{
        getProduct(page = 1){  //    page 1  getProduct() pagepage1
            const apiPath = process.env.VUE_APP_APIPATH ; // config/dev.env.jsAPIPATH
            const customPath = process.env.VUE_APP_CUSTOMPATH; // config/dev.env.jsCUSTOMPATH
            const vm =this ;
            const api = `${apiPath}/api/${customPath}/products?page=${page}`; // Api + 
            vm.$store.dispatch('updateLoading',true);
            this.$http.get(api).then((response) => {
                console.log(response.data);
                vm.$store.dispatch('updateLoading',false);
                vm.products = response.data.products;  
                vm.pagination = response.data.pagination;
            })
        },
        delProduct(){
            const apiPath = process.env.VUE_APP_APIPATH ; // config/dev.env.jsAPIPATH
            const customPath = process.env.VUE_APP_CUSTOMPATH; // config/dev.env.jsCUSTOMPATH
            const vm =this ;
            const api = `${apiPath}/api/${customPath}/admin/product/${vm.tempProduct.id}`;
            this.$http.delete(api).then((response) => { 
                console.log(response.data);
                if(response.data.success){
                    $('#delProductModal').modal('hide');   // modal
                    vm.getProduct() ;//  
                    this.$bus.$emit('message:push',response.data.message,'success'); // 
                }
                else{
                    $('#delProductModal').modal('hide');   // modal
                    vm.getProduct() ;//  
                    this.$bus.$emit('message:push',response.data.message,'danger'); // 
                }
                
            });            
        },
        uploadFile(){
            console.log(this); // VueComponent $refs.files.files[0] 
            
            const uploadedFile = this.$refs.files.files[0];
            const vm =this;
            const formData = new FormData(); //  formData  
            formData.append('file-to-upload',uploadedFile) ; // append  append('inputname', )
            const apiPath = process.env.VUE_APP_APIPATH ; // config/dev.env.jsAPIPATH
            const customPath = process.env.VUE_APP_CUSTOMPATH; // config/dev.env.jsCUSTOMPATH
            const url = `${apiPath}/api/${customPath}/admin/upload` ;
            vm.status.fileUploading=true;
            vm.$http.post(url,formData,{ // POSTurlformData
                header:{
                    'Content-Type': 'multipart/form-data' // form-data
                }
            }).then((response)=>{
                console.log('',response.data);  //   imageUrl
                vm.status.fileUploading=false;

                if(response.data.success){
                    // vm.tempProduct.imageUrl = response.data.imageUrl; // console  vm.tempProduct  set() get() 
                    // console.log(vm.tempProduct);
                    vm.$set(vm.tempProduct,'imageUrl',response.data.imageUrl)   // Vue.set vm.$set(,/key,) 
                    this.$bus.$emit('message:push',response.data.message,'success'); // 
                }
                else{
                    this.$bus.$emit('message:push',response.data.message,'danger'); // 
                }
            })

        },
        openModal(isNew,item){
            if(isNew){  // isNew data return  this.isNew
                this.tempProduct ={}; //  this.tempProduct  modal input 
                this.isNew = true ;   //  isNew  true
            }
            else{
                // Object.assign({},item) => ES6 item  1item1
                this.tempProduct =Object.assign({},item); // itemmodal this.tempProduct item modal input 
                this.isNew = false ;  //  isNew  false
            }
            $('#productModal').modal('show')
        },
        delModal(item){  //itemtempProduct delModal
            const vm =this;
            vm.tempProduct =Object.assign({},item);
            $('#delProductModal').modal('show')
        },
        updateProduct(){
            const apiPath = process.env.VUE_APP_APIPATH ; // config/dev.env.jsAPIPATH
            const customPath = process.env.VUE_APP_CUSTOMPATH; // config/dev.env.jsCUSTOMPATH
            const vm =this ;
            let httpMethod = 'post' ;
            let api = `${apiPath}/api/${customPath}/admin/product` ;  // api
            if(!vm.isNew){ //isNewfalse apiapi
                api = `${apiPath}/api/${customPath}/admin/product/${vm.tempProduct.id}` ; // /api/:api_path/admin/product/:id id tempProduct  id
                httpMethod = 'put' ; //  http put
            }
            this.$http[httpMethod](api,{data:vm.tempProduct}).then((response) => {  //   apidata  {data:vm.tempProduct} 
                console.log(response.data);
                if(response.data.success){  // 
                    $('#productModal').modal('hide')   // modal
                    vm.getProduct() ;// 
                    this.$bus.$emit('message:push',response.data.message,'success'); // 
                }
                else{
                    $('#productModal').modal('hide')   // modal
                    vm.getProduct() ;// 
                    this.$bus.$emit('message:push',response.data.message,'danger'); // 
                }
                
            })
        },
       
    },
    created(){
        this.getProduct();
        
    }
}
</script>