import axios from 'axios';

export default{
    namespaced:true,
    state:{
        products:[],
        pagination:{},   
        allProducts:[],     
    },
    mutations:{
        PRODUCTS(state,payload){
            state.products = payload;
        },
        PAGINATION(state,payload){
            state.pagination = payload;
        },
        ALLPRODUCTS(state,payload){
            state.allProducts = payload.sort(function(a,b){
                return a.category < b.category ? 1 : -1 ;
            });
        }        
    },
    actions:{
        getProducts(context,page=1){
            const apiPath = process.env.VUE_APP_APIPATH ; // config/dev.env.jsAPIPATH
            const customPath = process.env.VUE_APP_CUSTOMPATH; // config/dev.env.jsCUSTOMPATH
            context.commit('LOADING',true,{root: true});
            const api = `${apiPath}/api/${customPath}/products?page=${page}`; // Api 
            axios.get(api).then((response)=>{
                if (response.data.success){
                    context.commit('PRODUCTS',response.data.products) ;
                    context.commit('LOADING',false,{root: true});
                    context.commit('PAGINATION',response.data.pagination) ;
                }

            }) 
        }, 
        getAllProduct(context){ //    fillter computed
            const apiPath = process.env.VUE_APP_APIPATH ; // config/dev.env.jsAPIPATH
            const customPath = process.env.VUE_APP_CUSTOMPATH; // config/dev.env.jsCUSTOMPATH
            context.commit('LOADING',true,{root: true});
            const api = `${apiPath}/api/${customPath}/products/all`; // Api 
            axios.get(api).then((response) => {
                if (response.data.success) {
                context.commit('ALLPRODUCTS',response.data.products);
                context.commit('LOADING',false,{root: true});
                }
            });            
        },        
    },
    getters:{
        products(state){
            return state.products;
        },
        pagination(state){
            return state.pagination;
        },
        allProducts(state){
            return state.allProducts;
        }        
    }
}