<template>
    <div>
        <div class="my-5 row justify-content-center">
        <form class="col-md-6" @submit.prevent='payOrder'>
            <table class="table">
            <thead>
                <th></th>
                <th></th>
                <th></th>
            </thead>
            <tbody>
                <tr v-for="item in order.products" :key="item.id">
                <td class="align-middle">{{ item.product.title }}</td>
                <td class="align-middle">{{ item.qty }}/{{ item.product.unit }}</td>
                <td class="align-middle text-right">{{ item.final_total }}</td>
                </tr>
            </tbody>
            <tfoot>
                <tr>
                <td colspan="2" class="text-right"></td>
                <td class="text-right">{{ order.total }}</td>
                </tr>
            </tfoot>
            </table>

            <table class="table">
            <tbody>
                <tr>
                <th width="100">Email</th>
                <td>{{ order.user.email }}</td>
                </tr>
                <tr>
                <th></th>
                <td>{{ order.user.name }}</td>
                </tr>
                <tr>
                <th></th>
                <td>{{ order.user.tel }}</td>
                </tr>
                <tr>
                <th></th>
                <td>{{ order.user.address }}</td>
                </tr>
                <tr>
                <th></th>
                <td>
                    <span v-if="!order.is_paid"></span>
                    <span v-else class="text-success"></span>
                </td>
                </tr>
            </tbody>
            </table>
            <div class="text-right" v-if="order.is_paid === false">
            <button class="btn btn-danger"></button>
            </div>
        </form>
        </div>        
    </div>
</template>

<script>
export default {
    data:function(){
        return{
            orderId:'', // id
            order:{ // 
                user:{},  // email  user
                products:[],  //  products
            }, 
        }    
    },
    methods:{
        getOrder(){
            const vm =this ;
            const apiPath = process.env.VUE_APP_APIPATH ; // config/dev.env.jsAPIPATH
            const customPath = process.env.VUE_APP_CUSTOMPATH; // config/dev.env.jsCUSTOMPATH
            vm.isLoading = true;
            const api = `${apiPath}/api/${customPath}/order/${vm.orderId}`; // Api + 
            this.$http.get(api).then((response)=>{
                vm.order = response.data.order;
                
                
                console.log('',vm.order);
                
                vm.isLoading = false;
            }) 
        },
        payOrder(){
            const apiPath = process.env.VUE_APP_APIPATH ; // config/dev.env.jsAPIPATH
            const customPath = process.env.VUE_APP_CUSTOMPATH; // config/dev.env.jsCUSTOMPATH
            const vm =this ;
            vm.isLoading = true;
            const api = `${apiPath}/api/${customPath}/pay/${vm.orderId}`; // idApi 
            this.$http.post(api).then((response)=>{
                console.log('',response);
                if(response.data.success){
                    alert('');
                    vm.getOrder(); //
                }
                
                vm.isLoading = false;
            })             
        }
    },
    created(){
        this.orderId = this.$route.params.orderId  // id =  : 
        console.log('orderId',this.orderId);
        this.getOrder(); // 
    }
}
</script>