import axios from 'axios';

export default{
    namespaced:true,
    state:{
        carts:[],
        cartsData:{},
        status:{
            loadingItem:''
        },
        isdisabled:false, //  disabled 
    },
    actions:{
        getCart(context){ // 
            const apiPath = process.env.VUE_APP_APIPATH ; // config/dev.env.jsAPIPATH
            const customPath = process.env.VUE_APP_CUSTOMPATH; // config/dev.env.jsCUSTOMPATH
            context.commit('LOADING',true,{root: true});
            const api = `${apiPath}/api/${customPath}/cart`; // Api 
            axios.get(api).then((response)=>{
                if(response.data.success){
                    // console.log('',response);
                    context.commit('CARTS',response.data.data.carts);
                    context.commit('CARTSDATA',response.data.data);
                    context.commit('LOADING',false,{root: true});
                }
            })             
        }, 
        removeCart(context,id){ // id
            context.commit('ISDISABLED',true);
            const apiPath = process.env.VUE_APP_APIPATH ; // config/dev.env.jsAPIPATH
            const customPath = process.env.VUE_APP_CUSTOMPATH; // config/dev.env.jsCUSTOMPATH
            context.commit('LOADINGITEM',id);
            const api = `${apiPath}/api/${customPath}/cart/${id}`; // idApi 
            axios.delete(api).then((response)=>{
                if(response.data.success){
                    context.commit('LOADINGITEM','');
                    context.dispatch('getCart');
                    // vm.$bus.$emit('checkUpdateCart'); //
                    context.commit('ISDISABLED',false);
                    context.dispatch('updateMessage',{
                        message:response.data.message+ '',
                        status:'danger', 
                        },
                        {root: true});
                }

            })           
        }, 
        addtoCart(context,{id,qty = 1}){ // modal id1
            const apiPath = process.env.VUE_APP_APIPATH ; // config/dev.env.jsAPIPATH
            const customPath = process.env.VUE_APP_CUSTOMPATH; // config/dev.env.jsCUSTOMPATH
            context.commit('LOADINGITEM',id);
            context.commit('ISDISABLED',true);
            const cart = {
                product_id:id, //id
                qty, // 
            }
            const api = `${apiPath}/api/${customPath}/cart`;  // idApi
            axios.post(api,{data:cart}).then((response)=>{
                if(response.data.success){
                    // vm.$bus.$emit('updateCart');  // bus  header vm.$bus.$on('updateCart') headergetCart 
                    context.commit('LOADINGITEM','');
                    context.dispatch('getCart');
                    context.dispatch('updateMessage',{
                        message:'',
                        status:'success', 
                        },
                        {root: true});
                     context.commit('ISDISABLED',false);
                }
                else{
                    context.dispatch('updateMessage',{
                        message:response.data.message,
                        status:'danger', 
                        },
                        {root: true});                    
                     context.commit('ISDISABLED',false);
                }
            }) 
           
        },        
    },
    mutations:{
        CARTS(state,payload){
            state.carts = payload;
        },
        CARTSDATA(state,payload){
            state.cartsData = payload;
        },
        LOADINGITEM(state,payload){
            state.status.loadingItem = payload;
        },
        ISDISABLED(state,status){
            state.isdisabled = status;
        },
    },
    getters:{
        carts(state){
            return state.carts; 
        },
        cartsData(state){
            return state.cartsData; 
        },
        status(state){
            return state.status;
        },
        loadingItem(state){
            return state.status.loadingItem;
        },
        isdisabled(state){
            return state.isdisabled;
        },                        

    },

}