<template>
    <div class="search-container">
        <form class="form-inline">
        <div class="input-group">
            <input class="form-control " type="search" placeholder="" aria-label="Search" v-model="searchContent" @input.prevent="showMatch" @keydown.prevent="clickToMatch">
            <div class="input-group-append">
            <button class="btn btn-outline-warning" type="button" id="button-addon2" @click.prevent="clickToMatch" ><i class="fas fa-search"></i></button>
            </div>
        </div>
        </form> 
        <div class="search-result" v-show="searchContent">
            <table class='table mb-1'>
                <thead>
                    <tr class="border-0">
                        <th  colspan="2" class='text-muted' ></th>
                    </tr>
                </thead>
                <tbody v-if="searchContent">
                    <tr class="search-result-item border-0" v-for="result in searchMatch" :key="result.id" @click.prevent="toMatchDetail(result.id)">
                        <td> <b>{{result.title}}</b> </td>
                        <td class="text-warning text-right">{{result.price | currency}} </td>
                    </tr>
                </tbody>
                <tbody v-if="searchMatch.length === 0">
                    <tr class="border-0">
                        <td colspan="2" class="text-danger"></td>
                        
                    </tr>
                </tbody>                            
            </table>
        </div>
    </div>                
</template>


<script>
import $ from 'jquery';
import {mapActions,mapGetters} from 'vuex';
export default {
    data(){
      return{
        searchMatch:[], // match
        searchContent:'', // input
      }      
    },
    computed:{
        ...mapGetters('productsModules',['allProducts']),
    },
    methods:{
        ...mapActions('productsModules',['getAllProduct']),
        getMatch(){
            const vm =this;
            vm.searchMatch = vm.allProducts.filter(item=> item.title.match(vm.searchContent));
        },  
        showMatch(){  // list
            const vm =this;
            if(vm.searchContent === ''){
                $('.search-result').hide();
            }
            else{
                $('.search-result').show();
                 vm.getMatch();
            }
        },
        toMatchDetail(id){  
            const vm =this ;
            if (vm.$route.path === `/product_detail/${id}`) { // input
                vm.searchContent = '';
                return;
            }
            vm.$store.dispatch('updateLoading',true);// 
            vm.$router.push(`/product_detail/${id}`);
            if (vm.$route.name === 'ProductDetail') { // 
                window.location.reload(); // vue
            }
            vm.searchContent = '';
            vm.$store.dispatch('updateLoading',false);
        },
        clickToMatch(){ // 
            const vm =this;
            let result = vm.allProducts.find(item => item.title === vm.searchContent );
            if(result){  // 
                if(vm.$route.path === `/product_detail/${result.id}`){  // input
                    vm.searchContent = '';
                    return;                    
                }
                vm.$store.dispatch('updateLoading',true);
                vm.$router.push(`/product_detail/${result.id}`); 
                if (vm.$route.name === 'ProductDetail') { // 
                    window.location.reload(); // vue
                }
                vm.searchContent = '';
                vm.$store.dispatch('updateLoading',false);                 
            }
        }             
    },
    created(){
        const vm = this;
        vm.getAllProduct();
    }    
}
</script>

