<template>
    <div>
        <!-- <loading :active.sync="isLoading" ></loading>         -->
        <div class="row">
            <div class="col-md-4 mb-4" v-for='item in products' :key='item.id'>
                <div class="card border-0 shadow-sm">
                    <div style="height: 350px; background-size: cover; background-position: center "
                    :style="{backgroundImage:`url(${item.imageUrl})`}"
                    >
                    </div>
                    <div class="card-body">
                    <span class="badge badge-secondary float-right ml-2">{{item.category}}</span>
                    <h5 class="card-title">
                        <a href="#" class="text-dark">{{item.title}}</a>
                    </h5>
                    <p class="card-text">{{item.content}}</p>
                    <div class="d-flex justify-content-between align-items-baseline">
                        <!-- <div class="h5">2,800 </div> -->
                        <del class="h6"> {{item.origin_price | currency}} </del>
                        <div class="h5"> {{item.price | currency}} </div>
                    </div>
                    </div>
                    <div class="card-footer d-flex">
                    <button type="button" class="btn btn-outline-secondary btn-sm" @click = 'getProduct(item.id)'> 
                        <i class="fas fa-spinner fa-spin" v-if='status.loadingItem === item.id'></i>
                        
                    </button>
                    <button type="button" class="btn btn-outline-danger btn-sm ml-auto"
                    @click='addtoCart(item.id)'
                    >
                        <i class="fas fa-spinner fa-spin" v-if='status.loadingItem === item.id'></i>
                        
                    </button>
                    </div>
                </div>
            </div>            
        </div> 
        <Pagination  :paginationProp='pagination' @emit-update-page='getProducts'></Pagination>
        <hr>
        <!--  -->
        <div class="row d-flex justify-content-center"  v-if="carts.length !== 0">
            <div class="col-6">
                <table class="table" >
                    <thead>
                        <tr>
                            <th width="80"></th>
                            <th></th>
                            <th width="80"></th>
                            <th width="100"></th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="cart in carts" :key='cart.id'>
                            <td><button class="btn btn-outline-danger btn-sm" @click='removeCart(cart.id)'><i class="fas fa-trash-alt "></i></button></td>
                            <td>{{cart.product.title}}</td>
                            <td>{{cart.qty}} / {{cart.product.unit}} </td>
                            <td class="text-right">{{cart.product.price | currency}}</td>
                        </tr>
                    </tbody>
                    <tfoot>
                        <tr class="font-weight-bold">
                            <td colspan="3" class="text-right"></td>
                            <td class="text-right">{{cartsData.total | currency}}</td>
                        </tr>
                        <tr class="text-success font-weight-bold" v-if="cartsData.final_total !== cartsData.total">
                            <td colspan="3" class="text-right"></td>
                            <td class="text-right">{{cartsData.final_total | currency}}</td>
                        </tr>
                    </tfoot>
                </table>  
                <div class="input-group mb-3">
                    <input type="text" class="form-control" placeholder="" v-model='coupon_code' aria-label="Recipient's username" aria-describedby="button-addon2">
                    <div class="input-group-append">
                        <button class="btn btn-outline-secondary " type="button" id="button-addon2" @click='addCouponCode'></button>
                    </div>
                </div>                              
            </div>
            
        </div>
        <!--  -->
        <div class="my-5 row justify-content-center">
        <form class="col-md-6" @submit.prevent='createOrder'>
            <div class="form-group">
            <label for="useremail">Email <span class="text-danger">()</span></label>
            <input type="email" v-validate="'required|email'" class="form-control" name="email" id="useremail"
                :class="{'is-invalid':errors.has('email')}"
                v-model="form.user.email"  placeholder=" Email" 
                >
               
            <span class="text-danger" v-if="errors.has('email')">{{errors.first('email')}}</span>    
            
            
            </div>
        
            <div class="form-group">
            <label for="username"> <span class="text-danger">()</span></label>
            <input v-validate="'required'" type="text" class="form-control" name="name" id="username"
                v-model="form.user.name"  placeholder=""
                :class="{'is-invalid':errors.has('name')}"
                >
            <span class="text-danger" v-if="errors.has('name')"></span>
            </div>
        
            <div class="form-group">
            <label for="usertel"> <span class="text-danger">()</span></label>
            <input type="tel" v-validate="'required'" class="form-control" name='tel' id="usertel" :class="{'is-invalid':errors.has('tel')}" v-model="form.user.tel" placeholder="">
            <span class="text-danger"  v-if="errors.has('tel')"></span>
            </div>
        
            <div class="form-group">
            <label for="useraddress"> <span class="text-danger">()</span></label>
            <input v-validate="'required'" type="text" :class="{'is-invalid':errors.has('address')}" class="form-control" name="address" id="useraddress" v-model="form.user.address"
                placeholder="">
            <span class="text-danger"  v-if="errors.has('address')"></span>
            </div>
        
            <div class="form-group">
            <label for="comment"></label>
            <textarea name="" id="comment" class="form-control" cols="30" rows="10" v-model="form.message" placeholder=":"></textarea>
            </div>
            <div class="text-right">
            <button class="btn btn-danger"></button>
            </div>
        </form>
        </div>        

        <!-- Modal -->
        <div
        class="modal fade"
        id="productModal"
        tabindex="-1"
        role="dialog"
        aria-labelledby="productModalLabel"
        aria-hidden="true"
        >
            <div class="modal-dialog" role="document">
                <div class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title" id="productModalLabel">{{ product.title }}</h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    <img :src="product.imageUrl" class="img-fluid" alt />
                    <blockquote class="blockquote mt-3">
                    <p class="mb-0">{{ product.content }}</p>
                    <footer class="blockquote-footer text-right">{{ product.description }}</footer>
                    </blockquote>
                    <div class="d-flex justify-content-between align-items-baseline">
                    <div class="h5 ml-auto" v-if="!product.price">{{ product.origin_price | currency }}</div>
                    <del class="h6" v-if="product.price"> {{ product.origin_price | currency }}</del>
                    <div class="h5" v-if="product.price"> {{ product.price | currency }}</div>
                    </div>
                    <select class="form-control mt-3" v-model="product.num">
                    <option :value='num' v-for='num in 10' :key='num' > {{num}} {{product.unit}} </option>
                    </select>
                </div>
                <div class="modal-footer">
                    <div class="text-muted text-nowrap mr-3" v-if="product.num>=1">
                    
                    <strong>{{ product.num * product.price }}</strong> 
                    </div>
                    <button
                    type="button"
                    class="btn btn-primary"
                    @click='addtoCart(product.id,product.num)'
                    ></button>
                </div>
                </div>
            </div>
        </div>              
    </div>
</template>


<script>
import Pagination from '../../../components/Pagination' //Pagination 
import $ from 'jquery';

export default {
    data:function(){
        return{
            products:[], //
            product:{},  //
            carts:[], //
            cartsData:{}, // 
            isLoading:false,
            status:{
                loadingItem:'' //idloading
            },
            coupon_code:'' , //model
            form:{  //
                user: {
                    name: '',
                    email:'' ,
                    tel: '',
                    address: ''
                },
                message:''
            },
            pagination:{}
        }
    },
    components:{
        Pagination
    },    
    methods:{
        getProducts(page = 1){
            const apiPath = process.env.VUE_APP_APIPATH ; // config/dev.env.jsAPIPATH
            const customPath = process.env.VUE_APP_CUSTOMPATH; // config/dev.env.jsCUSTOMPATH
            const vm =this ;
            vm.$store.dispatch('updateLoading',true);
            const api = `${apiPath}/api/${customPath}/products?page=${page}`; // Api 
            this.$http.get(api).then((response)=>{
                vm.products = response.data.products.sort(function(a,b){
                    return a.category<b.category ? 1 : -1;
                })
                console.log('',vm.products);
                vm.$store.dispatch('updateLoading',false);
                vm.pagination = response.data.pagination;
            }) 
        },
        getProduct(id){ //  id
            const apiPath = process.env.VUE_APP_APIPATH ; // config/dev.env.jsAPIPATH
            const customPath = process.env.VUE_APP_CUSTOMPATH; // config/dev.env.jsCUSTOMPATH
            const vm =this ;
            vm.status.loadingItem = id; // id
            const api = `${apiPath}/api/${customPath}/product/${id}`;  // idApi
            this.$http.get(api).then((response)=>{
                vm.product = response.data.product;
                $('#productModal').modal('show');
                console.log('',vm.product);
                vm.status.loadingItem = ''; // modal vm.status.loadingItem loading
            }) 
        },
        addtoCart(id,qty = 1){ // modal id1
            const apiPath = process.env.VUE_APP_APIPATH ; // config/dev.env.jsAPIPATH
            const customPath = process.env.VUE_APP_CUSTOMPATH; // config/dev.env.jsCUSTOMPATH
            const vm =this ;
            vm.status.loadingItem = id; // id
            const cart = {
                product_id:id, //id
                qty, // 
            }
            const api = `${apiPath}/api/${customPath}/cart`;  // idApi
            this.$http.post(api,{data:cart}).then((response)=>{
                console.log('',response);
                vm.status.loadingItem = ''; // modal vm.status.loadingItem loading
                vm.getCart(); // 
                $('#productModal').modal('hide');  // modal
            }) 
        },
        getCart(){ // 
            const apiPath = process.env.VUE_APP_APIPATH ; // config/dev.env.jsAPIPATH
            const customPath = process.env.VUE_APP_CUSTOMPATH; // config/dev.env.jsCUSTOMPATH
            const vm =this ;
            vm.$store.dispatch('updateLoading',true);
            const api = `${apiPath}/api/${customPath}/cart`; // Api 
            this.$http.get(api).then((response)=>{
                // console.log('',response);
                vm.carts = response.data.data.carts;
                vm.cartsData = response.data.data ;
                console.log('',vm.carts);
                vm.$store.dispatch('updateLoading',false);
            })             
        },
        removeCart(id){ // id
            const apiPath = process.env.VUE_APP_APIPATH ; // config/dev.env.jsAPIPATH
            const customPath = process.env.VUE_APP_CUSTOMPATH; // config/dev.env.jsCUSTOMPATH
            const vm =this ;
            vm.$store.dispatch('updateLoading',true);
            const api = `${apiPath}/api/${customPath}/cart/${id}`; // idApi 
            this.$http.delete(api).then((response)=>{
                console.log('',response);
                vm.getCart(); //
                vm.$store.dispatch('updateLoading',false);
            })           
        },
        addCouponCode(){
            const apiPath = process.env.VUE_APP_APIPATH ; // config/dev.env.jsAPIPATH
            const customPath = process.env.VUE_APP_CUSTOMPATH; // config/dev.env.jsCUSTOMPATH
            const vm =this ;
            const coupon = {code:vm.coupon_code};
            vm.$store.dispatch('updateLoading',true);
            const api = `${apiPath}/api/${customPath}/coupon`; // Api 
            this.$http.post(api,{data:coupon}).then((response)=>{
                console.log('',response);
                vm.getCart(); //
                vm.$store.dispatch('updateLoading',false);
            })              
        },
        createOrder(){
            const apiPath = process.env.VUE_APP_APIPATH ; // config/dev.env.jsAPIPATH
            const customPath = process.env.VUE_APP_CUSTOMPATH; // config/dev.env.jsCUSTOMPATH
            const vm =this ;
            const api = `${apiPath}/api/${customPath}/order`; // Api , message , user 
            vm.$store.dispatch('updateLoading',true);
            const order = vm.form
            this.$validator.validate().then((result) => { // vee-validate
                if (result) {
                    this.$http.post(api,{data:order}).then((response)=>{
                        console.log('',response);
                        vm.$store.dispatch('updateLoading',false);
                        vm.$router.push(`/customer_check/${response.data.orderId}`) //
                    })  
                }
                else{
                    alert('');
                }
            }); 
   
            
          
        }       
    },
    created(){
        this.getProducts();
        this.getCart();
        
    },
}
</script>