<template>
    <div class="container">
        <Processbar process = 'checkOrder' v-if="!order.is_paid"></Processbar>
        <Processbar process="pay" v-if="order.is_paid"></Processbar>
        <div class="my-5 row justify-content-center">
            <div class="col-lg-6 col-md-8">
                <!--  -->
                <section class="accordion card-header-shadow border-0 mb-3" id="accordionExample" >

                    <div class="card" >
                        <div class="card-header border bg-primary" id="headingOne">
                            <h2 class="mb-0 ">
                                <button class="btn btn-link text-white" type="button" data-toggle="collapse"
                                    data-target="#collapseOne" aria-expanded="true" aria-controls="collapseOne">
                                    
                                </button>
                            </h2>
                        </div>
                        <!--  .show  -->
                        <div id="collapseOne" class="collapse show" aria-labelledby="headingOne"
                            data-parent="#accordionExample">
                            <div class="card-body bg-secondary">
                                <table class="table table-responsive-md">
                                    <thead>
                                        <tr class="text-warning">
                                            <th class="align-middle" width="100"></th>
                                            <th class="align-middle"></th>
                                            <th class="align-middle" width="80"></th>
                                            <th class="align-middle" width="80"></th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr v-for="product in products"   :key='product.id' class="text-white">
                                            <td class="align-middle">
                                                <img class="img-fluid"
                                                    :src="product.product.imageUrl"
                                                    alt="">
                                            </td>
                                            <td class="align-middle">{{product.product.title}}</td>
                                            <td class="align-middle">{{product.qty}}/{{product.product.unit}}</td>
                                            <td class="align-middle text-right">{{product.final_total | currency}}</td>
                                        </tr>
                                    </tbody>
                                    <tfoot>
                                        <tr class="text-white" v-if="order.total<3000">
                                            <td colspan="3" class="text-right"><b></b></td>
                                            <td class="text-right"><b>$ 60</b></td>
                                        </tr>
                                        <tr class="text-white" v-if="order.total>=3000">
                                            <td colspan="3" class="text-right"><b></b></td>
                                            <td class="text-right"><del><b>$ 60</b></del></td>
                                        </tr>                                        
                                        <tr class="text-danger" v-if="order.total<3000">
                                            <td colspan="3" class="text-right"><b></b> </td>
                                            <td class="text-right"><b>{{order.total + 60 |currency}}</b></td>
                                        </tr>
                                        <tr class="text-danger" v-if="order.total>=3000">
                                            <td colspan="3" class="text-right"><b></b> </td>
                                            <td class="text-right"><b>{{order.total |currency}}</b></td>
                                        </tr>                                        
                                    </tfoot>
                                </table>
                            </div>
                        </div>
                        <div class="bg-secondary ">
                            <h3 class="text-warning text-center mt-2"></h3>
                            <table class="table ">
                                <tbody class="text-white">
                                    <tr>
                                        <th></th>
                                        <td>{{user.name}}</td>
                                    </tr>
                                    <tr>
                                        <th></th>
                                        <td>{{user.email}}</td>
                                    </tr>
                                    <tr>
                                        <th></th>
                                        <td>{{user.tel}}</td>
                                    </tr>   
                                    <tr>
                                        <th></th>
                                        <td>{{user.address}}</td>
                                    </tr>   
                                    <tr>
                                        <th></th>
                                        <td class="text-danger" v-if="!order.is_paid"><b></b></td>
                                        <td class="text-success" v-if="order.is_paid"><b></b></td>
                                    </tr>                                                                                                                   
                                </tbody>
                            </table>   
                            <div class="text-right mb-2 mx-3 " v-if="!order.is_paid">
                                <button class="btn btn-danger w-100-mobile" @click="payOrder()"></button>
                            </div>                            
                        </div>                                                  
                    </div>
                </section>   
            </div>
        </div>
    </div>
</template>

<script>
import Processbar from '../../../components/Frontdesk/Processbar'


export default {
    data(){
        return{
            orderId:'',
            order:{},
            products:[], // 
            user:{}, // 
            isLoading : false,              
        }
    },
    components:{
        Processbar,

    },
    methods:{
        getOrder(){
            const vm =this ;
            const apiPath = process.env.VUE_APP_APIPATH ; // config/dev.env.jsAPIPATH
            const customPath = process.env.VUE_APP_CUSTOMPATH; // config/dev.env.jsCUSTOMPATH
            vm.isLoading = true;
            const api = `${apiPath}/api/${customPath}/order/${vm.orderId}`; // Api + 
            this.$http.get(api).then((response)=>{
                vm.order = response.data.order;
                vm.products =  response.data.order.products;
                vm.user = response.data.order.user;
                vm.isLoading = false;
            }) 
        },
        payOrder(){
            const apiPath = process.env.VUE_APP_APIPATH ; // config/dev.env.jsAPIPATH
            const customPath = process.env.VUE_APP_CUSTOMPATH; // config/dev.env.jsCUSTOMPATH
            const vm =this ;
            vm.isLoading = true;
            const api = `${apiPath}/api/${customPath}/pay/${vm.orderId}`; // idApi 
            this.$http.post(api).then((response)=>{
                if(response.data.success){
                    vm.getOrder(); //
                    vm.$bus.$emit('message:push',response.data.message ,'success')
                }
                vm.isLoading = false;
            })             
        }        
    },   
    created(){
        const vm =this;
        vm.orderId = vm.$route.params.orderId ; // id
        vm.getOrder();
    }     
    
}
</script>

<style lang="css">
@media(max-width: 767px){
    .w-100-mobile{
        width: 100%;
    }
}
    
</style>