<template>
    <div class="modal mt-5 " id="eventAlert" tabindex="-1" role="dialog" >
        <div class="modal-dialog modal-max-width " role="document">
            <div class="modal-content ">
                <div class="modal-header bg-danger ">
                    <h5 class="modal-title text-white "> {{seconds}} </h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    <p class="h3 text-danger text-center"> <b>108!</b> </p>
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-danger" data-dismiss="modal"></button>
                    <button type="button" class="btn btn-primary" @click.prevent="toEvent"></button>
                </div>
            </div>
        </div>
    </div> 
</template>

<script>
import $ from 'jquery';


export default {
    data(){
        return{
            show : false, // mountedmodal
            seconds: 3, // 
        }
    },
    methods:{
        alertEvent(){ 
            const vm =this;
            if(!vm.show){  // modal
                $('#eventAlert').modal('show');
                vm.show = true;
                vm.closeModal();
            }
        },
        closeModal(){
            const vm =this;
            if(vm.show){ // modal
                setTimeout(function(){
                    $('#eventAlert').modal('hide');
                    vm.show =  false;
                },3000)
                setTimeout(function(){  //  3 2 1
                    vm.seconds = 2
                },1000)
                setTimeout(function(){
                    vm.seconds = 1
                },2000)
            }
        },
        toEvent(){ // modal  modaldisabled
            const vm =this;
            $('#eventAlert').modal('hide');
            vm.show =  false;
            vm.$router.push('/event');
        }
    },
    mounted(){
    const vm =this;
    vm.alertEvent();
    }    
}
</script>

