											<td class="align-middle text-right">{{cart.total | currency}}</td>
										</tr>
									</tbody>
									<tfoot>
										<!--  3000 -->
										<tr class="text-white" v-if="cartsData.total<3000 & !isCoupon">
											<td colspan="4" class="text-right"  ><b></b></td>
											<td class="text-right"><b>$ 60</b></td>
										</tr>
										<!--  3000header colspan=3 -->
										<tr class="text-white" v-if="cartsData.total<3000 & isCoupon">
											<td colspan="3" class="text-right"  ><b></b></td>
											<td class="text-right"><b>$ 60</b></td>
										</tr>                                        
										<tr class="text-white" v-if="cartsData.total>=3000">
											<td colspan="4" class="text-right" v-if="!isCoupon"><b></b></td>
											<td colspan="3" class="text-right" v-if="isCoupon"><b></b></td>
											<td class="text-right"><del><b>$ 60</b></del></td>
										</tr> 
										<tr class="text-danger" v-if="cartsData.total>=3000">
											<td colspan="4" class="text-right" v-if="!isCoupon"><b></b> </td>
											<td colspan="3" class="text-right" v-if="isCoupon"><b></b> </td>
											<td class="text-right" v-if="!isCoupon"><b>0</b></td>
											<td class="text-right" v-if="isCoupon"><b>{{cartsData.total - cartsData.final_total |currency}}</b></td>
										</tr> 
											<!--  3000 -->                                                                               
										<tr class="text-danger" v-if="cartsData.total<3000 & !isCoupon">
											<td colspan="4" class="text-right" ><b></b> </td>
											<td class="text-right"><b>{{cartsData.total + 60 |currency}}</b></td>
										</tr>
										<!--  3000header colspan=3 -->
										<tr class="text-danger" v-if="cartsData.total<3000 & isCoupon ">
											<td colspan="3" class="text-right" ><b></b> </td>
											<td class="text-right"><b>{{cartsData.total + 60 |currency}}</b></td>
										</tr>                                        
										<tr class="text-danger" v-if="cartsData.total>=3000">
											<td colspan="4" class="text-right" v-if="!isCoupon"><b></b> </td>
											<td colspan="3" class="text-right" v-if="isCoupon"><b></b> </td>
											<td class="text-right" v-if="!isCoupon"><b>{{cartsData.total |currency}}</b></td>
											<td class="text-right" v-if="isCoupon"><b>{{cartsData.final_total |currency}}</b></td>
										</tr>   
									</tfoot>
								</table>
							</div>
						</div>
						<!--  -->
						<div class="alert  bg-warning text-center alert-rounded text-white mt-3" role="alert" v-if="cartsData.total<3000">{{3000-cartsData.total |currency}}</div>
						<div class="alert  bg-dark text-center alert-rounded text-danger mt-3 font-weight-bold" role="alert" v-if="cartsData.total>=3000 & !isCoupon"></div>
						<div class="alert  bg-success text-center alert-rounded text-white mt-3 font-weight-bold" role="alert" v-if="cartsData.total>=3000 & isCoupon"></div>
						<router-link to="/products" class="btn btn-danger mt-2" ></router-link >
					</div>
				</section>  
				<div class="input-group mb-3 " v-if="cartsData.total>=3000 & !isCoupon" >
						<input type="text" class="form-control" placeholder="" v-model='coupon_code' aria-label="Recipient's username" aria-describedby="button-addon2" >
						<div class="input-group-append">
								<button class="btn btn-outline-warning " type="button" id="button-addon2" @click='addCouponCode' ></button>
						</div>
				</div>
				<!--  -->
				<form class="text-white" @submit.prevent='createOrder' v-if="carts.length > 0" >
					<div class="form-group">
					<label for="username"><span class="h5"></span> <span class="text-danger"><b>()</b></span></label>
					<input 
						v-validate="'required'" 
						type="text" class="form-control" 
						name="name" id="username"
						v-model="form.user.name"  
						placeholder=""
						:class="{'is-invalid':errors.has('name')}"
					>
					<span class="text-danger" v-if="errors.has('name')"></span>
					</div>

					<div class="form-group">
					<label for="useremail"><span class="h5"></span> <span class="text-danger"><b>()</b></span></label>
					<input type="email" v-validate="'required|email'" class="form-control" name="email" id="useremail"
							:class="{'is-invalid':errors.has('email')}"
							v-model="form.user.email"  placeholder=" Email" 
							>
					<span class="text-danger" v-if="errors.has('email')">{{errors.first('email')}}</span>
					</div>    

					<div class="form-group">
						<label for="usertel"><span class="h5"></span> <span class="text-danger"><b>()</b></span></label>
						<input type="tel" v-validate="'required'" class="form-control" name='tel' id="usertel" :class="{'is-invalid':errors.has('tel')}" v-model="form.user.tel" placeholder="">
						<span class="text-danger"
							v-if="errors.has('tel')"
						>
						</span>
					</div>

					<div class="form-group">
					<label for="useraddress"><span class="h5"></span> <span class="text-danger"><b>()</b></span></label>
					<input v-validate="'required'" type="text" 
						:class="{'is-invalid':errors.has('address')}" 
						class="form-control" 
						name="address" 
						id="useraddress" 
						v-model="form.user.address"
						placeholder=""
					>
					<span class="text-danger"  v-if="errors.has('address')"></span>
					</div>

					<div class="form-group">
					<label for="comment"><span class="h5"></span></label>
					<textarea name="" id="comment" class="form-control" cols="30" rows="10" v-model="form.message" placeholder=":"></textarea>
					</div>
					<div class="text-right">
							<button class="btn btn-primary"></button>
					</div>
